<template>
<div class="Base" style="left:0%;top:0%;height: 100%;width:100%">
    <el-container>
        <el-header style="background-color: #4fc08d;color:white;border-radius:10px">
            <span style="font-family: STCaiyun;font-size:larger;">{{TimeNow}}</span>
            <div style="position:relative;left:93%;width:7%;top:-10px">{{UserName}}</div>
        </el-header>
            <el-container>

                <el-aside style="background-color:#4fc08d;width:170px;height:90vh ">
                    <el-menu v-bind:router="true" class="el-menu-vertical" mode="vertical"  background-color="#ffffff" text-color="#4fc08d" active-text-color="black">
                        <el-menu-item index="/login"><span class="el-icon-key">              登陆      </span></el-menu-item>
                        <el-menu-item index="/file"><span class="el-icon-folder-opened  ">   文件      </span></el-menu-item>
                        <el-menu-item index="/chat"><span class="el-icon-chat-dot-round">       聊天室    </span></el-menu-item>
                        <el-menu-item index="4"><span class="el-icon-postcard">             留言板    </span></el-menu-item>
                        <el-menu-item index="First"><span class="el-icon-moon">             开发ing   </span></el-menu-item>
                    </el-menu>        
                </el-aside>

                <el-main @keypress.native.enter="getSqliteData">
                   
                        <div v-html="DataHTML" class="window">

                        </div>
                        <el-input style="width:92%" v-model="DataBaseOrder"></el-input>
                        <el-button @click="getSqliteData" type="success">
                            Submit
                        </el-button>
                   



                </el-main>

            </el-container>
    </el-container>
</div>
</template>

<script>
    export default {
        name: 'Base',
        data() {
            return {
                UserName: " 诶嘿  不好意思  你是谁来着?",
                TimeNow: new Date().toLocaleString(),
                UserToken: window.localStorage.getItem("token"),
                DataHTML: null,
                DataBaseOrder: ""
            }
        },
        mounted() {
            this.TimeNow = setInterval(() => {
                this.TimeNow = new Date().toLocaleString();
            }, 1000)
            this.CheckLogin();
            this.UserName = window.localStorage.getItem("UserName");
        },
        beforeDestroy() {
            if (this.TimeNow) {
                clearInterval(this.TimeNow);
            }
        },
        methods: {
            CheckLogin: function() {
                if (this.UserToken == null) {
                    window.alert("非嘉心糖禁止入内~");
                    this.$router.push('/login')
                }
            },
            getSqliteData: function() {
                var requestURL = "http://81.68.100.77:2333/admin/data"
                if (this.DataBaseOrder == null) {
                    this.$message("不能发送空内容哦~");
                    return;
                }
                this.$http.get(requestURL, {
                        /* params headers需被包裹在{}内*/
                        headers: {
                            "token": this.UserToken,
                        },
                        params: {
                            db: "data.db",
                            order: this.DataBaseOrder,
                        }
                    })
                    .then(response => {
                        if (response.data.status == 1) {
                            this.DataHTML = response.data.data;
                        } else {
                            this.$message(" 指令错误！ ");
                        }
                        this.DataBaseOrder = null;
                    })
                    .catch(error => {
                        this.$message("出现了奇怪的BUG，请检查下网络？");
                    })
            }
        },

    }
</script>

<style>
    .el-menu-vertical {
        height: 100%
    }
    
    .window {
        border: #4fc08d solid;
        height: 77vh;
        margin-bottom: 1%;
        border-radius: 5px;
        overflow: auto;
    }
    
    #app>div>section>section>main>div.window>table>thead>tr>th {
        background-color: #4fc08d;
        color: white;
        width: 10%;
    }
    
    #app>div>section>section>main>div.window>table>tbody>tr:nth-child(2n+1)>td {
        table-layout: fixed;
        height: 7vh;
        color: #4fc08d;
        font-size: large;
    }
    
    #app>div>section>section>main>div.window>table>tbody>tr:nth-child(2n)>td {
        background-color: #a2a7a51e;
        height: 7vh;
        color: #4fc08d;
        font-size: large;
    }
</style>


