import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import Login from '@/components/login'
import File from '@/components/file'
import Chat from '@/components/chat'
import Sqlite from '@/components/sqlite'
Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'index',
        component: index,
        /* 通过export default导出的内容赋给了index 将index作为组件注册在这个路由上？ */
    }, {
        path: '/login',
        name: 'login',
        component: Login,
    }, {
        path: '/file',
        name: 'file',
        component: File,
    }, {
        path: '/chat',
        name: 'chat',
        component: Chat,
    }, {
        path: '/admin/data',
        name: 'sqlite',
        component: Sqlite,
    }]
})


