<template>
<div class="Chat" style="left:0%;top:0%;height: 100%;width:100%">
    <el-container >
        <el-header style="background-color: #4fc08d;color:white;border-radius:10px">
            <span style="font-family: STCaiyun;font-size:larger;">{{TimeNow}}</span>
            <div style="position:relative;left:93%;width:7%;top:-10px">{{UserName}}</div>
        </el-header>
            <el-container>

                <el-aside style="background-color:#4fc08d;width:170px;height:90vh ">
                    <el-menu v-bind:router="true" class="el-menu-vertical" mode="vertical"  background-color="#ffffff" text-color="#4fc08d" active-text-color="black">
                        <el-menu-item index="login"><span class="el-icon-key">              登陆      </span></el-menu-item>
                        <el-menu-item index="file"><span class="el-icon-folder-opened  ">   文件      </span></el-menu-item>
                        <el-menu-item index="chat"><span class="el-icon-chat-dot-round">    聊天室    </span></el-menu-item>
                        <el-menu-item index="4"><span class="el-icon-postcard">             留言板    </span></el-menu-item>
                        <el-menu-item index="First"><span class="el-icon-moon">             开发ing   </span></el-menu-item>
                    </el-menu>        
                </el-aside>

                <el-main>
                <el-tabs tab-position="left" style="height: 84vh;">
                    <el-tab-pane label="Public" @keypress.native.enter="SendMessageToServe">
                        <div class="window">
                                <div v-for="message in UserPublicMessageList">
                                    <img class="avator" :src="UserAvatar + message.id" @dblclick="SendMessageToCreateRoom(message.id)" />
                                        <div class="username">{{message.username}}</div>
                                        <div style="width: 88%">
                                                <span style="color: #ffffff;font-size:20px" class="message">
                                                    &nbsp;{{message.message}}&nbsp; 
                                                </span>
                                        </div>
                                </div>
                        </div>

                            <div style="position: relative;height:8vh;top:2vh;left:17%;width:80%">
                                <el-input  placeholder="输入消息" v-model="UserMessage" style="width:70%"></el-input>
                                <el-button type="primary" @click="SendMessageToServe">Submit</el-button>
                                <el-button type="success" @click="GetPublicHistoryMessage();drawer=true">History</el-button>
                            </div>
                            <el-drawer :visible.sync="drawer">
                                <div v-for="history in HistoryMessage">
                                    <div v-for="iter in history" >
                                        <div style="margin-bottom:50px;">
                                            <span style="font-size: 20px;color:#4fc08d;font-weight:800">{{iter.user_name}}   :   {{iter.message}}</span>
                                            <p style="font-size:small;font-weight:100;float:right;">{{iter.time}}</p>
                                        </div>
                                    </div>
                                </div>
                            </el-drawer>    
                    </el-tab-pane>
                    <!------------------------------------------------------------------------------------------------>
                    <el-tab-pane v-for="(key ,value, index) in PrivateObject" :label="key.UserName" @keypress.native.enter="SendPrivateMessage(key.RoomID)">
                        <div class="window">
                                <div v-for="message in key.MessageList">
                                    <img class="avator" :src="UserAvatar + message.id" />
                                        <div class="username">{{message.username}}</div>
                                        
                                        <div style="width: 88%">
                                                <span style="color: #ffffff;font-size:20px" class="message">
                                                    &nbsp;{{message.message}}&nbsp; 
                                                </span>
                                        </div>
                                </div>
                        </div>

                            <div style="position: relative;height:8vh;top:2vh;left:17%;width:80%">
                                <el-input  placeholder="输入消息" v-model="UserPrivateMessage" style="width:70%"></el-input>
                                <el-button type="primary" @click="SendPrivateMessage(key.RoomID)">Submit</el-button>
                                <el-button type="success" @click="GetPrivateHistoryMessage(key.RoomID);drawer=true">History</el-button>
                            </div>

                            <el-drawer :visible.sync="drawer" :before-close="handleClose" :wrapperClosable="drawer" :modal-append-to-body="!drawer">
                                <div v-for="history in HistoryMessage">
                                    <div v-for="iter in history">
                                        <div style="margin-bottom:50px;">
                                            <span style="font-size: 20px;color:#4fc08d;font-weight:800">{{iter.user_name}}   :   {{iter.message}}</span>
                                            <p style="font-size:small;font-weight:100;float:right;">{{iter.time}}</p>
                                        </div>
                                    </div>
                                </div>
                            </el-drawer>
                    </el-tab-pane>
                    <!------------------------------------------------------------------------------------------------>
            </el-tabs>
                    

                </el-main>

            </el-container>
    </el-container>
</div>
</template>
<script>
    /*记得限制一下用户 前后端都要 禁止用户和自己加入私聊房间*/
    export default {
        name: 'Chat',
        data() {
            return {
                UserName: " 诶嘿  不好意思  你是谁来着?",
                TimeNow: new Date().toLocaleString(),
                UserToken: window.localStorage.getItem("token"),
                UserMessage: "", //public
                UserPrivateMessage: "",
                NameSpace: "public",
                UserPublicMessageList: new Array(),
                UserPrivateMessageList: new Array(),
                PrivateObject: new Object(), // 对象的每一个属性对应一个数组 数组内是消息
                UserAvatar: 'http://81.68.100.77:2333/auth/avatar?token=' + window.localStorage.getItem("token") + '&username=',
                RoomID: "",
                HistoryMessage: "",
                // PrivateMessageObject: new Object(),
                drawer: false,
            }
        },
        mounted() {
            this.TimeNow = setInterval(() => {
                this.TimeNow = new Date().toLocaleString();
            }, 1000);
            /*this.TimeNow = setInterval(() => {
                document.querySelector("#pane-0 > div.window").scrollTop = document.querySelector("#pane-0 > div.window").scrollHeight;;
            }, 2000);*/
            this.CheckLogin();
            this.$socket.connect();
            this.$socket.emit("login", {
                "token": this.UserToken,
            })
        },
        beforeDestroy() {
            if (this.TimeNow) {
                clearInterval(this.TimeNow);
            }
        },
        methods: {
            CheckLogin: function() {
                if (this.UserToken == null) {
                    window.alert("~能~不~能~先~登~录~呢~");
                    this.$router.push('/login')
                };
                if (window.localStorage.getItem("UserName") != null) {
                    this.UserName = window.localStorage.getItem("UserName");
                };
            },
            SendMessageToServe: function() {
                if (this.UserMessage != null) {
                    //this.$socket.emit('public', this.UserToken + "$" + this.UserMessage);
                    this.$socket.emit('public', {
                        "message": this.UserMessage,
                    })
                    this.UserMessage = null;
                } else {
                    this.$message("不允许发送空内容哦");
                }
                setTimeout(() => {
                    document.querySelector("#pane-0 > div.window").scrollTop = document.querySelector("#pane-0 > div.window").scrollHeight;;
                }, 100);
            },
            SendMessageToCreateRoom: function(UserID) { /*双击头像创建房间*/
                this.$socket.emit('create', UserID);
            },
            SendPrivateMessage: function(RoomID) {
                if (this.UserPrivateMessage != null) {
                    this.$socket.emit('private', {
                        "room_id": RoomID,
                        "message": this.UserPrivateMessage,
                    });
                    this.UserPrivateMessage = null;
                } else {
                    this.$message("不允许发送空内容哦");
                };
                setTimeout(() => {
                    document.querySelector("#pane-0 > div.window").scrollTop = document.querySelector("#pane-0 > div.window").scrollHeight;;
                }, 100)
            },
            GetPublicHistoryMessage: function() {
                var requestUrl = "http://81.68.100.77:2333/chat/message";
                this.$http.get(requestUrl, {
                        /* params headers需被包裹在{}内*/
                        headers: {
                            "token": this.UserToken,
                        },
                        params: {
                            "room": "public",
                        }
                    })
                    .then(response => {
                        if (response.data.status == 1) {
                            this.HistoryMessage = response.data.data;
                        } else {
                            this.$message("会话过期 请重新登录")
                        }
                    })
                    .catch(error => {
                        this.$message("出现了奇怪的错误 要不检查下网络？");
                    })
            },
            GetPrivateHistoryMessage: function(RoomID) {
                var requestUrl = "http://81.68.100.77:2333/chat/message";
                this.$http.get(requestUrl, {
                        /* params headers需被包裹在{}内*/
                        headers: {
                            "token": this.UserToken,
                        },
                        params: {
                            "room": RoomID,
                        }
                    })
                    .then(response => {
                        if (response.data.status == 1) {
                            this.HistoryMessage = response.data.data;
                        } else {
                            this.$message("会话过期 请重新登录")
                        }
                    })
                    .catch(error => {
                        this.$message("出现了奇怪的错误 要不检查下网络？");
                    })
            },
            handleClose: function() {
                this.drawer = false;
            }
        },
        sockets: {
            public: function(data) {
                this.UserPublicMessageList.push(Object(data));
            },
            private: function(data) {
                this.PrivateObject[data.room_id].MessageList.push({
                    "id": data.user_id,
                    "username": data.user_name,
                    "message": data.message,
                });
                //this.UserPrivateMessageList.push(Object(data));
            },
            notice: function(data) {
                var PaneName = "";
                if (data.User1 == this.UserName) {
                    PaneName = data.User2;
                } else {
                    PaneName = data.User1;
                }
                this.RoomID = data.RoomID;
                this.PrivateObject[data.RoomID] = {
                    "UserName": PaneName,
                    "RoomID": this.RoomID,
                    "MessageList": new Array(),
                };
                /* this.PrivateMessageObject[this.RoomID] = new Array(); //创建一个聊天消息数组
                 
                         将聊天对象及其对应房间ID存入数据库
                     },*/
            },
            /* updated() {

                 //   
            document.querySelector("#app > div > section > section > main > div.window").scrollTop = document.querySelector("#app > div > section > section > main > div.window").scrollHeight + 5000
        },*/

        }
    }
</script>

<style>
    .el-menu-vertical {
        height: 100%
    }
    
    .window {
        border: solid #4fc08d;
        border-radius: 17px;
        height: 72vh;
        width: 90%;
        position: relative;
        left: 5%;
        overflow: auto;
    }
    
    .message {
        background-color: #4fc08d;
        position: relative;
        left: 12vh;
        top: -5vh;
        border-radius: 5px;
    }
    
    .username {
        position: relative;
        left: 12vh;
        top: -5.5vh;
        width: 7vh;
        font-size: 2px;
    }
    
    .avator {
        background-color: #4fc08d;
        position: relative;
        left: 2vh;
        height: 7vh;
        width: 5%;
        top: 10px;
        border-radius: 100%;
    }
</style>
<style>
    .el-drawer.rtl {
        overflow: scroll;
    }
</style>
</style>
