<template>
<div class="File" style="left:0%;top:0%;height: 100%;width:100%;overflow-x: hidden;">
    <el-container>
        <el-header style="background-color: #4fc08d;color:white;border-radius:10px">
            <span style="font-family: STCaiyun;font-size:larger;">{{TimeNow}}</span>
            <div style="position:relative;left:93%;width:7%;top:-10px">{{UserName}}</div>
        </el-header>
            <el-container>

                <el-aside style="background-color:#4fc08d;width:170px;height:90vh ">
                    <el-menu v-bind:router="true" class="el-menu-vertical" mode="vertical"  background-color="#ffffff" text-color="#4fc08d" active-text-color="black">
                        <el-menu-item index="login"><span class="el-icon-key">              登陆      </span></el-menu-item>
                        <el-menu-item index="file"><span class="el-icon-folder-opened  ">   文件      </span></el-menu-item>
                        <el-menu-item index="chat"><span class="el-icon-chat-dot-round">    聊天室    </span></el-menu-item>
                        <el-menu-item index="4"><span class="el-icon-postcard">             留言板    </span></el-menu-item>
                        <el-menu-item index="First"><span class="el-icon-moon">             开发ing   </span></el-menu-item>
                    </el-menu>        
                </el-aside>
                
                <el-main>
                <el-tabs stretch="true">
                <el-tab-pane label="上传">
                    <el-upload
                    class="upload-demo"
                    drag
                    auto-upload
                    action="http://81.68.100.77:2333/file/upload"
                    :headers="this.headers"
                    multiple>
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                    <!-- <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div> -->
                    </el-upload>
                </el-tab-pane>
                <el-tab-pane label="下载" >
                    <div  class="file_list_div">
                            <div class="file_list" v-for="name in FileList">
                                <div>
                                    <span style="position:relative;top:20%;margin:20px">{{name}}</span>
                                    <el-button style="float:right;margin-right:20px" type="danger" @click="delFile(name)"> Delete   </el-button>
                                    <el-button style="float:right;margin-right:20px" type="primary" @click="getFile(name)"> Download </el-button>
                                </div>
                            </div>
                        </div>
                </el-tab-pane>
            
                </el-tabs>
                </el-main>
            </el-container>
    </el-container>
</div>
</template>

<script>
    export default {
        name: 'File',
        data() {
            return {
                UserName: " 诶嘿  不好意思  你是谁来着?",
                TimeNow: new Date().toLocaleString(),
                UserToken: window.localStorage.getItem("token"),
                FileList: "",
                headers: "",
            }
        },
        mounted() {
            this.TimeNow = setInterval(() => {
                this.TimeNow = new Date().toLocaleString();
            }, 1000);
            this.CheckLogin();
            this.UserName = window.localStorage.getItem("UserName");
            this.headers = {
                "token": this.UserToken,
            };
            this.getFileList();
        },
        beforeDestroy() {
            if (this.TimeNow) {
                clearInterval(this.TimeNow);
            }
        },
        methods: {
            CheckLogin: function() {
                if (this.UserToken == null) {
                    this.$message("非嘉心糖禁止入内~");
                    this.$router.push('/login');
                };
                if (window.localStorage.getItem("UserName") != null) {
                    this.UserName = window.localStorage.getItem("UserName");
                };
            },
            getFile(filename) {
                //var requestURL = 'http://172.81.239.150:2333/file/download';
                var requestURL = 'http://81.68.100.77:2333/file/download';
                const a = document.createElement('a')
                a.download = filename
                a.href = requestURL + '?token=' + window.localStorage.getItem("token") + "&filename=" + filename;
                document.body.appendChild(a)
                a.click()
                document.body.removeChild(a)
            },
            delFile(filename) {
                //var requestURL = 'http://172.81.239.150:2333/file/download';
                var requestURL = 'http://81.68.100.77:2333/file/delete';
                this.$http({
                        method: 'delete',
                        url: requestURL,
                        params: {
                            token: window.localStorage.getItem("token"),
                            filename: filename,
                        }
                    })
                    .then(response => {
                        if (response.data.status == 1) {
                            this.getFileList();
                        } else {
                            this.$message("你没有权限进行该操作~");
                        }
                    })
                    .catch(error => {
                        this.$message("出现了奇怪的BUG 请检查下网络？");
                    })

            },
            getFileList: function() {
                // var requestURL = 'http://172.81.239.150:2333/file/list';
                var requestURL = "http://81.68.100.77:2333/file/list";
                this.$http.get(requestURL, {
                        /* params headers需被包裹在{}内*/
                        headers: {
                            "token": this.UserToken,
                        }
                    })
                    .then(response => {
                        if (response.data.status == 1) {
                            this.FileList = response.data.data;
                        } else {
                            if (response.data.message == "NoFile") {
                                this.$message("文件不存在！");
                            } else {
                                this.$message("会话过期,请重新登录!");
                            }
                        }
                    })
                    .catch(error => {
                        this.$message("出现了奇怪的BUG，请检查下网络？");
                    })
            }
        },

    }
</script>

<style>
    .el-menu-vertical {
        height: 100%
    }
    
    .file_list_div {
        z-index: 777;
        position: relative;
        top: 10%;
        height: 67vh;
        left: 5%;
        width: 97%;
        overflow-x: hidden;
    }
    
    .file_list {
        position: relative;
        margin-top: 10px;
        width: 90%;
        height: 50px;
        /*border: solid #4fc08d;*/
        border-radius: 3px;
    }
    
    .file_list div {
        display: block;
        transition-duration: 1.2s;
        background-color: #4fc08d;
        color: #fff;
        height: 100%;
        cursor: default;
        border-radius: 10px;
    }
    
    .file_list div:hover {
        background-color: white;
        color: #4fc08d;
    }
</style>


