<template>
<div class="Login" style="left:0%;top:0%;height: 100%;width:100%">
    <el-container>
        <el-header style="background-color: #4fc08d;color:white;border-radius:10px">
            <span style="font-family: STCaiyun;font-size:larger;">{{TimeNow}}</span>
            <div style="position:relative;left:93%;width:7%;top:-10px">{{UserName}}</div>
        </el-header>
            <el-container>

                <el-aside style="background-color:#4fc08d;width:170px;height:90vh">
                    <el-menu :default-active="2" router=true class="el-menu-vertical" mode="vertical"  background-color="#ffffff" text-color="#4fc08d" active-text-color="black">
                        <el-menu-item index="login"><span class="el-icon-key">              登陆      </span></el-menu-item>
                        <el-menu-item index="file"><span class="el-icon-folder-opened  ">   文件      </span></el-menu-item>
                        <el-menu-item index="chat"><span class="el-icon-chat-dot-round">    聊天室    </span></el-menu-item>
                        <el-menu-item index="4"><span class="el-icon-postcard">             留言板    </span></el-menu-item>
                        <el-menu-item index="First"><span class="el-icon-moon">             开发ing   </span></el-menu-item>
                    </el-menu>        
                </el-aside>

                <el-main>
                    <el-tabs v-model="ActiveName" type="border-card" >
                        <el-tab-pane label="Login" @keypress.native.enter="LoginToGetToken">
                                <ul></ul>
                                <el-input v-model="UserID" placeholder="ID"></el-input>
                                <ul></ul>
                                <el-input v-model="PassWord" placeholder="密码" show-password="true"></el-input>
                                <ul></ul>
                                <el-button type="success" @click="LoginToGetToken" round >Submit</el-button>
                        </el-tab-pane>

                        <el-tab-pane label="Register" @keypress.native.enter="RegisterToGetAccount">
                                <ul></ul>
                                <el-input v-model="UserID" placeholder="ID"></el-input>
                                <ul></ul>
                                <el-input v-model="username" placeholder="用户昵称"></el-input>
                                <ul></ul>
                                <el-input v-model="PassWord" placeholder="密码" show-password="true"></el-input>
                                <ul></ul>
                                <!-- <el-input v-model="Sex" placeholder="性别" show-password="true"></el-input> -->
                                <el-input v-model="Phone" placeholder="电话号码" ></el-input>
                                <ul></ul>
                                <el-input v-model="Email" placeholder="邮箱" ><slot></slot></el-input>
                                <ul></ul>
                                <el-switch
                                v-model="Sex"
                                active-text="♂"
                                inactive-text="♀"
                                inactive-color="#4fc08d"
                                style="display: block;">
                                </el-switch>
                                <ul></ul>
                                <!-- <el-input v-model="Birth" placeholder="生日"></el-input> -->
                                <el-date-picker
                                v-model="Birth"
                                type="date"
                                placeholder="生日">
                                </el-date-picker>
                                <ul></ul>
                                
                                <el-button type="primary"  @click="RegisterToGetAccount" round>Submit</el-button>
                        </el-tab-pane>

                       
                        <el-tab-pane label="头像">
                            <el-upload
                            drag
                            auto-upload
                            action="http://81.68.100.77:2333/auth/avatar"
                            :headers="this.headers"
                            multiple>
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            <!-- <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div> -->
                            </el-upload>
                        </el-tab-pane>
                                
                    </el-tabs>


                    
                </el-main>

            </el-container>
    </el-container>
</div>
</template>


<script>
    export default {
        name: 'Login',
        data() {
            return {
                UserName: " 诶嘿  不好意思  你是谁来着?",
                username: "",
                UserID: "",
                PassWord: "",
                Sex: true,
                Phone: "",
                Email: "",
                Birth: "",
                TimeNow: new Date().toLocaleString(),
                UserToken: "",
                headers: {
                    "token": window.localStorage.getItem("token")
                },
            }
        },
        methods: {
            LoginToGetToken: function() {
                //var requestUrl = 'http://172.81.239.150:2333/auth/login';
                var requestUrl = 'http://81.68.100.77:2333/auth/login';
                var formData = new FormData(); /* 不知道为什么Content-Type无法正确序列化表单数据 故在此使用表单类直接提交*/
                if (!parseInt(this.UserID)) {
                    this.$message("ID格式错误! ");
                    return;
                }
                if (this.PassWord == null) {
                    this.$message("密码不能为空! ");
                    return;
                }
                formData.append("user_id", this.UserID);
                formData.append("password", this.PassWord);
                this.$http.post(requestUrl, formData)
                    .then(response => {
                        if (response.data.status === 1) {
                            this.UserToken = response.data.token;
                            this.UserName = response.data.user_name;
                            window.localStorage.setItem('token', this.UserToken); /* 储存到本地 后续使用window.localStorage.getItem("token ")来获取token*/
                            window.localStorage.setItem('UserName', this.UserName);
                            this.$message("登陆成功！ ");
                            this.$router.push('/file')
                        } else {
                            this.$message("登陆失败，请检查用户名与密码是否匹配 ");
                            window.location.reload();
                        }
                    })
                    .catch(error => {
                        if (error.status === 200) {
                            this.$message("登陆失败，请检查用户名与密码是否匹配 ");
                        } else {
                            this.$message("请检查网络是否正常！ ");
                        }
                    })
            },
            RegisterToGetAccount: function() {
                //var requestUrl = 'http://172.81.239.150:2333/auth/register';
                var requestUrl = 'http://81.68.100.77:2333/auth/register';
                var formData = new FormData(); /* 不知道为什么Content-Type无法正确序列化表单数据 故在此使用表单类直接提交*/
                if (!parseInt(this.UserID)) {
                    this.$message("ID格式错误! ");
                    return;
                }
                if (this.PassWord == null) {
                    this.$message("密码不能为空! ");
                    return;
                }
                if (this.username == null) {
                    this.$message("用户昵称不能为空! ");
                    return;
                }
                formData.append("user_id", this.UserID);
                formData.append("password", this.PassWord);
                formData.append("user_name", this.username);
                formData.append("phone", this.Phone);
                formData.append("email", this.Email);
                formData.append("birth", this.Birth);
                if (this.Sex == true) {
                    formData.append("sex", 1);
                } else {
                    formData.append("sex", 0);
                }
                this.$http.post(requestUrl, formData)
                    .then(response => {
                        if (response.data.status === 1) {
                            window.alert("注册成功！ ");
                            this.$router.push('/login')
                        } else {
                            this.$message("注册失败 用户已存在！");
                            window.location.reload();
                        }
                    })
                    .catch(error => {
                        if (error.status === 200) {
                            this.$message("注册失败，发生了奇怪的错误！");
                        } else {
                            this.$message("请检查网络是否正常！ ");
                        }
                    })
            }
        },
        mounted() {
            this.TimeNow = setInterval(() => {
                this.TimeNow = new Date().toLocaleString();
            }, 1000);
            if (window.localStorage.getItem("UserName") != null) {
                this.UserName = window.localStorage.getItem("UserName");
            };
        },
        beforeDestroy() {
            if (this.TimeNow) {
                clearInterval(this.TimeNow);
            };

        },
    }
</script>

<style>
    .el-menu-vertical {
        height: 100%
    }
</style>


